/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}
/* 2. Remove default margin */
* {
  margin: 0;
  padding: 0;
}
body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}
/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
  border: none;
}
/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/* ACTUAL CSS */

:root {
  --bg-color: #22223b;
  --medium-bg-color: #4a4e69;
  --light-bg-color: #9a8c98;
  --accent-color: #c9ada7;
  --text-color: #f2e9e4;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Poppins, sans-serif;
  color: var(--text-color);
  background: var(--bg-color);
  height: 100vh;
  width: 100vw;
}

.main-window {
  display: flex;
  flex-direction: column;

  aspect-ratio: 16/9;
  width: 100%;
  max-width: 1800px;
  border: 1px solid var(--accent-color);
}

button {
  box-shadow: 2px 4px 10px rgba(34, 34, 59, 0.8);
  white-space: nowrap;
  cursor: pointer;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background: var(--medium-bg-color);
  border-bottom: 2px solid var(--accent-color);
  padding-left: 40px;
  height: 10%;
}

header h1 {
  letter-spacing: 0.1rem;
}

.burger-menu {
  position: relative;
  width: 35px;
  height: 35px;
  margin-right: 40px;
  display: none;
}

.burger-menu span {
  position: absolute;
  width: 80%;
  height: 3px;
  background: var(--text-color);
}

.burger-menu span:first-child {
  left: 10%;
  top: 25%;
}
.burger-menu span:nth-child(2) {
  left: 10%;
  top: 50%;
}
.burger-menu span:last-child {
  left: 10%;
  top: 75%;
}

main {
  position: relative;
  display: flex;
  height: 90%;
}

nav {
  display: flex;
  flex-direction: column;

  color: var(--text-color);
  background: var(--bg-color);
  font-size: 1.2rem;
  height: 100%;
  width: 25%;
  border-right: 2px solid var(--accent-color);
  padding-left: 40px;
}

nav ul {
  display: flex;
  flex-direction: column;

  height: 50%;
  padding-left: 20px;
  list-style: none;
}

nav ul:first-child {
  justify-content: space-evenly;
  height: 40%;
}

nav ul:first-child li {
  cursor: pointer;
}

nav > p {
  display: flex;
  justify-content: space-between;
  align-items: center;
  user-select: none;
  margin-top: 20px;
}

#addProject {
  position: relative;

  background: var(--light-bg-color);
  vertical-align: baseline;
  padding: 0 0.6rem;
  padding-top: 2px;
  border-radius: 100%;
  margin-right: 30px;
  cursor: pointer;
}

#addProject:hover {
  scale: 1.1;
}

#addProject:hover::before {
  content: "Add Project";
  font-size: 0.8rem;
  background: var(--light-bg-color);
  padding: 0 8px;
  border-radius: 0.3rem;
  white-space: nowrap;
  scale: 1;

  position: absolute;
  top: -35px;
  left: -30px;
}

.project-nav {
  gap: 10px;
  padding-top: 20px;
  padding-right: 40px;
  overflow-y: auto;
}

.project-nav li p {
  cursor: pointer;
}

.project-nav-element {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  /* flex-wrap: wrap; */

  position: relative;
}

.nav-project-name-container {
  overflow: hidden;
  width: 50%;
}

.nav-project-name-container p {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.project-options-container > * {
  cursor: pointer;
}

.project-options-container {
  display: flex;
  justify-content: flex-end;
  gap: 20%;
  width: 50%;
  position: relative;
}

.project-options-container i:hover,
.todo-options-container i:hover {
  scale: 1.15;
}

.project-nav-element p,
nav ul:first-child li {
  display: inline-block;
  transition-duration: 200ms;
}

nav ul:first-child li.selected-project,
li.selected-project p {
  margin-left: 20px;
}

.input-container,
.rename-project-container {
  width: 100%;
}

.project-name-input,
.rename-project-input {
  width: 100%;
  margin-bottom: 5px;
}

.nav-button-container,
.rename-project-button-container,
.delete-project-button-container,
.title-request-button-container,
.edit-todo-button-container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  gap: 40px;

  width: 100%;
  margin-top: 10px;
}

.title-request-button-container {
  display: flex;
  justify-content: center;

  width: 100%;
  margin-top: 10px;
}

.add-button,
.cancel-button,
.rename-button,
.delete-button,
.add-todo-button,
.edit-todo-button,
.ok-button {
  display: inline-block;
  text-align: center;
  color: var(--text-color);
  width: 50%;
  padding: 4px 10px;
  border-radius: 16px;
}

.add-button,
.rename-button,
.add-todo-button,
.edit-todo-button,
.ok-button {
  background-color: green;
}

.delete-button {
  background-color: red;
}

.cancel-button {
  background-color: var(--light-bg-color);
}

.verify-delete-container,
.title-request-container {
  background: var(--medium-bg-color);
  border: 1px solid var(--accent-color);
  border-radius: 1rem;
  box-shadow: 30px 40px 40px rgba(34, 34, 59, 1);
  padding: 40px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: default;
}

.warning-message {
  color: red;
  text-shadow: 2px 4px 10px rgba(34, 34, 59, 1);
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
}

.verify-delete-container button,
.title-request-container button {
  margin-top: 30px;
}

.content {
  display: flex;
  flex-direction: column;

  background: rgba(74, 78, 105, 0.5);
  width: 75%;
}

.project-name-container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background: var(--bg-color);
  border-bottom: 2px solid var(--accent-color);
  padding: 30px 40px;
}

.sort-container {
  display: flex;
  gap: 20px;

  font-size: 0.9em;
}

.sort-by-name,
.sort-by-priority,
.sort-by-date {
  font-weight: 500;
  cursor: pointer;
}

.selected-sort {
  text-decoration: underline;
  scale: 1.2;
}

.add-todo-container,
.edit-todo-form-container {
  display: flex;
  gap: 20px;

  padding: 10px 0 10px 40px;
  cursor: pointer;
  transition-duration: 250ms;
}

.add-todo-container {
  font-size: 1.2rem;
  background: var(--light-bg-color);
  color: var(--bg-color);
  border: 1px solid var(--bg-color);
  border-radius: 0.5rem;
  margin: 10px;
}

.add-todo-form,
.edit-todo-form {
  display: flex;
  flex-direction: column;
  gap: 10px;

  padding: 10px 40px;
  width: 100%;
}

.add-todo-form > div,
.edit-todo-form > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

input::placeholder,
textarea::placeholder {
  color: var(--text-color);
}

input,
textarea,
select {
  background: var(--light-bg-color);
  width: 75%;
  padding: 5px 10px;
  border: 1px solid var(--bg-color);
  border-radius: 0.5rem;
  box-shadow: inset 1px 1px 4px var(--bg-color);
}

textarea {
  resize: none;
}

.add-todo-project-container {
  display: flex;
  justify-content: flex-start;
}

.add-todo-project-container select {
  width: 25%;
  padding: 5px 20px;
}

.add-todo-form > .add-todo-button-container,
.edit-todo-form > .edit-todo-button-container {
  align-self: flex-end;
  display: flex;
  justify-content: flex-end;
  width: 30%;
}

.priority-buttons-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;

  width: 75%;
}

.no-priority-button,
.low-priority-button,
.medium-priority-button,
.high-priority-button {
  --button-color: transparent;
  text-align: center;
  border: 1px solid var(--button-color);
  width: 20%;
  padding: 5px 20px;
}

.low-priority-button,
.medium-priority-button,
.high-priority-button {
  color: var(--button-color);
  cursor: pointer;
  border-radius: 0.5rem;
}

.low-priority-button {
  --button-color: green;
}

.medium-priority-button {
  --button-color: orange;
}
.high-priority-button {
  --button-color: red;
}

.low-priority-button:hover,
.low-priority-button.selected,
.medium-priority-button:hover,
.medium-priority-button.selected,
.high-priority-button:hover,
.high-priority-button.selected {
  background: var(--button-color);
  color: var(--text-color);
  border-color: var(--text-color);
}

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 20px;

  padding: 20px;
  overflow-y: auto;
  height: 100%;
  margin: 10px;
}

*::-webkit-scrollbar {
  width: 10px;
  padding: 20px;
}

*::-webkit-scrollbar-track {
  background: var(--medium-bg-color);
  border-radius: 1rem;
}

*::-webkit-scrollbar-thumb {
  background: var(--accent-color);
  border-radius: 4px;
  cursor: pointer;
}

.todo-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  background-color: var(--medium-bg-color);
  border: 1px solid var(--accent-color);
  border-radius: 1rem;
  width: 100%;
  height: 4rem;
  cursor: default;
  box-shadow: 2px 4px 10px rgba(34, 34, 59, 1);
}

.todo-container:hover {
  box-shadow: 4px 8px 20px rgba(34, 34, 59, 1);
}

.todo-content-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;

  padding: 10px 20px;
  width: 100%;
}

.todo-content-container.done {
  text-decoration: line-through;
  color: var(--light-bg-color);
}

.todo-container:has(.todo-content-container.done) {
  box-shadow: none;
  border: 1px solid var(--bg-color);
}

.todo-container:has(.todo-content-container.done):hover {
  box-shadow: none;
}

.status-container input {
  width: 18px;
  height: 18px;
  box-shadow: 2px 4px 5px rgba(34, 34, 59, 0.8);
  margin-top: 6px;
}

.status-container input:checked {
  accent-color: var(--bg-color);
  color: var(--text-color);
  box-shadow: none;
}

.done {
  user-select: none;
}

.title-description-container {
  display: flex;
  flex-direction: column;
  width: 30%;
  max-width: 30%;
  height: 100%;
  cursor: pointer;
}

.title-description-container h3 {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.done > .title-description-container {
  cursor: default;
}

.description-container {
  overflow: hidden;
  padding-right: 20px;
}

.description-is-closed {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.todo-project-container {
  display: flex;
  gap: 10px;
  width: 20%;
  height: 100%;
}

.todo-container > .selected {
  cursor: default;
  user-select: none;
}

.right-side-todo-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 40px;
  width: 50%;
}

.right-side-todo-container > .priority {
  cursor: default;
}

.done > .right-side-todo-container > .priority:hover {
  background-color: inherit;
  color: var(--button-color);
  border: 1px solid var(--button-color);
}

.priority,
.todo-options-container {
  white-space: nowrap;
}

.due-date {
  flex: 2;
}

.priority {
  text-align: center;
  flex: 1;
}

.todo-options-container {
  flex: 1;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
}

.todo-options-container > * {
  cursor: pointer;
}

.todo-content-container.done .todo-options-container {
  color: var(--text-color);
}

@media only screen and (max-width: 1023px) {
  .main-window {
    aspect-ratio: unset;
    height: 100%;
    width: 100%;
  }

  .burger-menu {
    display: block;
  }

  nav {
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    width: 40%;
  }

  .unhide {
    display: flex;
  }

  .content {
    width: 100%;
  }
}
