;\n}\n\n@font-face {\n  font-family: \"Poppins\";\n  src: url(\"./fonts/Poppins-Regular.woff2\") format(\"woff2\"),\n    url(\"./fonts/Poppins-Regular.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n}\n\n/* 1. Use a more-intuitive box-sizing model */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n/* 2. Remove default margin */\n* {\n  margin: 0;\n  padding: 0;\n}\nbody {\n  /* 3. Add accessible line-height */\n  line-height: 1.5;\n  /* 4. Improve text rendering */\n  -webkit-font-smoothing: antialiased;\n}\n/* 5. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n/* 6. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n  border: none;\n}\n/* 7. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n/* 8. Improve line wrapping */\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n/* ACTUAL CSS */\n\n:root {\n  --bg-color: #22223b;\n  --medium-bg-color: #4a4e69;\n  --light-bg-color: #9a8c98;\n  --accent-color: #c9ada7;\n  --text-color: #f2e9e4;\n}\n\nbody {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  font-family: Poppins, sans-serif;\n  color: var(--text-color);\n  background: var(--bg-color);\n  height: 100vh;\n  width: 100vw;\n}\n\n.main-window {\n  display: flex;\n  flex-direction: column;\n\n  aspect-ratio: 16/9;\n  width: 100%;\n  max-width: 1800px;\n  border: 1px solid var(--accent-color);\n}\n\nbutton {\n  box-shadow: 2px 4px 10px rgba(34, 34, 59, 0.8);\n  white-space: nowrap;\n  cursor: pointer;\n}\n\nheader {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n\n  background: var(--medium-bg-color);\n  border-bottom: 2px solid var(--accent-color);\n  padding-left: 40px;\n  height: 10%;\n}\n\nheader h1 {\n  letter-spacing: 0.1rem;\n}\n\n.burger-menu {\n  position: relativ